class LoginComponent {
    constructor(userService, router) {
        this.userService = userService;
        this.router = router;
        // Neue Fragen einreichen & anderen Helfen
        // Fragen favorisieren, um sie spter leichter zu finden
        // Fragen bearbeiten, solltest du eine einfachere Antwort haben
        // Fragen & Antworten bewerten
        // Kommentare unter Fragen posten
        // Mit anderen <span class="gradient">Pros</span> diskutieren
        this.currentSlide = 0;
        this.slides = [
            {
                title: 'Fragen einreichen',
                subtitle: 'Durch das einreichen neuer Fragen hilfst du deinen Kommilitonen und vergrerst Richies Wissen',
                img: {
                    background: "url('https://i.giphy.com/media/iFU36VwXUd2O43gdcr/source.gif')"
                }
            },
            {
                title: 'Fragen favorisieren',
                subtitle: 'Hierduch kannst du ganz schnell gespeicherte Fragen abrufen und bentigst keine Lesezeichen in deinem Browser',
                img: {
                    background: "url('https://i.giphy.com/media/XreQmk7ETCak0/source.gif')"
                }
            },
            {
                title: 'Fragen bearbeiten',
                subtitle: 'Du kennst eine verstndlichere Antwort? Dann kannst du bereits existierende Fragen bearbeiten und so deinen Kommilitonen beim Lernen helfen',
                img: {
                    background: "url('https://i.giphy.com/media/3o6ZsV3A5DdPm0e5bO/giphy.webp')"
                }
            },
            {
                title: 'Fragen kommentieren & diskutieren',
                subtitle: 'Verfasse Kommentare unter Fragen und diskutiere mit deinen Kommilitonen ber Lsungsanstze oder fhre die Antwort weiter aus, um zu einem leichteren Verstndnis beizutragen',
                img: {
                    background: "url('https://i.giphy.com/media/lz67zZWfWPsWnuGH0s/source.gif')"
                }
            }
        ];
    }
    ngOnInit() {
        this.startSlider();
    }
    ngAfterViewInit() {
        if (this.userService.richieUser.signedIn) {
            this.router.navigate(['/settings']);
            return;
        }
    }
    ngOnDestroy() {
        clearInterval(this.interval);
    }
    startSlider() {
        this.interval = setInterval(() => {
            this.currentSlide = ++this.currentSlide % this.slides.length;
        }, 5000);
    }
}


